<template>
  <div
    class="cargo-insurance"
  >
    <div
      class="form-wrapper"
    >
      <v-form
        class="d-flex flex-column"
        :class="{ 'full-height justify-space-between': $vuetify.breakpoint.mobile }"
        :disabled="isFormReadOnly"
        @submit.prevent
      >
        <div>
          <base-field-file
            v-model="form.values.certificate_file"
            label="Certificate of Insurance "
            :is-required="true"
            :rules="form.certificateRules"
            :disabled="isFormReadOnly"
            clearable
            :append-icon="form.values.certificate_file ? 'mdi-open-in-new' : ''"
            :error="Boolean(serverError)"
            :error-messages="serverError"
            @click:append="openCertificate"
            @change="onFileChange"
          />

          <base-field-datepicker
            v-model="form.values.expiration_date"
            label="Expiration date "
            :is-required="true"
            :error="Boolean(serverError)"
            :error-messages="serverError"
            :rules="form.exparationDateRules"
          />

          <base-field-input
            type="string"
            v-model="form.values.cargo_limit"
            label="Cargo limit "
            :is-required="true"
            prefix="$"
            :error="Boolean(serverError)"
            :error-messages="serverError"
            :rules="form.cargoLimitRules"
          />

          <base-field-input
            type="string"
            v-model="form.values.deductible"
            label="Deductible "
            :is-required="true"
            prefix="$"
            :error="Boolean(serverError)"
            :error-messages="serverError"
            :rules="form.deductibleRules"
          />

          <base-field-input
            v-model="form.values.agent_name"
            label="Agent name "
            :is-required="true"
            :error="Boolean(serverError)"
            :error-messages="serverError"
            :rules="form.nameRules"
          />

          <base-field-input
            mask="+1 (###) ###-####"
            v-model="form.values.agent_phone"
            label="Agent number "
            :is-required="true"
            :error="Boolean(serverError)"
            :error-messages="serverError"
            :rules="form.phoneRules"
          />
        </div>

        <v-btn
          v-if="!isFormReadOnly"
          :width="$vuetify.breakpoint.mobile ? '100%' : 100"
          :height="$vuetify.breakpoint.mobile ? 50 : 35"
          depressed
          color="primary"
          class="mt-2"
          @click="onSubmitProfileCargoInsurance"
        >
          Save
        </v-btn>
      </v-form>
    </div>
  </div>
</template>

<script>
import BaseFieldInput from '@components/BaseFieldInput';
import BaseFieldFile from '@components/BaseFieldFile';
import BaseFieldDatepicker from '@components/BaseFieldDatepicker';
import {
  min,
  decimal,
  required,
} from '@utils/helpers/validationRules';

import getFileFromUrl from '@utils/helpers/getFileFromUrl';
import getFileNameFromUrl from '@utils/helpers/getFileNameFromUrl';
import enableClickOnButtonsInsideDisabledInput from '@/mixins/disabled-input-button-clickable';
import numeral from 'numeral';
import * as constants from '@modules/profile/constants';
import { createNamespacedHelpers } from 'vuex';

const { mapState, mapActions, mapMutations } = createNamespacedHelpers('profile');

export default {
  name: 'ProfileCargoInsurance',
  components: {
    BaseFieldInput,
    BaseFieldFile,
    BaseFieldDatepicker,
  },
  mixins: [enableClickOnButtonsInsideDisabledInput],
  watch: {
    'form.values.cargo_limit': {
      handler(next) {
        const dotIdx = String(next).indexOf('.');
        if (dotIdx !== -1 && next.slice(dotIdx + 1).length) {
          const format = `0,0.${'0'.repeat(next.slice(dotIdx + 1).length)}`;
          this.form.values.cargo_limit = numeral(next).format(format);
        } else {
          this.form.values.cargo_limit = numeral(next).format();
        }
      },
    },
    'form.values.deductible': {
      handler(next) {
        const dotIdx = String(next).indexOf('.');
        if (dotIdx !== -1 && next.slice(dotIdx + 1).length) {
          const format = `0,0.${'0'.repeat(next.slice(dotIdx + 1).length)}`;
          this.form.values.deductible = numeral(next).format(format);
        } else {
          this.form.values.deductible = numeral(next).format();
        }
      },
    },
    'form.values.certificate_file': {
      handler(next) {
        if (next.name !== this.fileName) this.isNewCertificate = true;
      },
    },
    'form.values': {
      deep: true,
      handler() {
        this.serverError = '';
        Object.keys(this.serverErrors).forEach(
          (key) => {
            this.serverErrors[key] = '';
          },
        );
      },
    },
  },
  props: {
    isFormReadOnly: {
      type: Boolean,
      required: true,
    },
  },
  data: () => ({
    form: {
      values: {
        agent_name: '',
        expiration_date: '',
        cargo_limit: '',
        deductible: '',
        agent_phone: '',
        certificate_file: null,
      },
      nameRules: [required('Name')],
      phoneRules: [required('Phone')],
      cargoLimitRules: [
        required('Cargo Limit'),
        (v) => decimal('Cargo Limit')(v.replaceAll(',', '')),
        (v) => min(1000)(+v.replaceAll(',', '')),
      ],
      deductibleRules: [
        required('Deduplicable'),
        (v) => decimal('Deduplicable')(v.replaceAll(',', '')),
        (v) => min(1000)(+v.replaceAll(',', '')),
      ],
      certificateRules: [required('Certificate')],
      exparationDateRules: [required('Exparation Date')],
    },
    isNewCertificate: false,
    serverErrors: {},
    serverError: '',
  }),
  computed: {
    ...mapState({
      carrierInsurance: (state) => state.carrierInsurance.data,
    }),
    fileName() {
      return getFileNameFromUrl(this.carrierInsurance.certificate_file);
    },
  },
  async beforeMount() {
    this.form.values = {
      agent_name: this.carrierInsurance.agent_name,
      agent_phone: this.carrierInsurance?.agent_phone.startsWith('+1') ? this.carrierInsurance.agent_phone.slice(2) : this.carrierInsurance.agent_phone,
      cargo_limit: this.carrierInsurance.cargo_limit,
      deductible: this.carrierInsurance.deductible,
      expiration_date: this.carrierInsurance.expiration_date,
      certificate_file: await getFileFromUrl(this.carrierInsurance.certificate_file, this.fileName),
    };
  },
  methods: {
    ...mapMutations({
      setCarrierInsurance: constants.mutations.UPDATE_CARRIER_INSURANCE,
    }),
    onFileChange(file) {
      this.form.values.certificate_file = file;
    },
    openCertificate() {
      window.open(this.carrierInsurance.certificate_file, '_blank');
    },
    ...mapActions({
      updateCarrierInsurance: constants.actions.EDIT_CARRIER_INSURANCE,
    }),
    onSubmitProfileCargoInsurance() {
      this.form.values.cargo_limit = numeral(this.form.values.cargo_limit).value();
      this.form.values.deductible = numeral(this.form.values.deductible).value();

      const profileCargoInsuranceFormData = { ...this.form.values };

      if (!this.isNewCertificate) delete profileCargoInsuranceFormData.certificate_file;

      this.updateCarrierInsurance({ data: profileCargoInsuranceFormData }).then(async (res) => {
        // const newVal = {
        //   agent_name: res.data.agent_name,
        //   agent_phone: res.data.agent_phone.startsWith('+1') ? res.data.agent_phone.slice(2) : res.data.agent_phone,
        //   cargo_limit: res.data.cargo_limit,
        //   deductible: res.data.deductible,
        //   expiration_date: res.data.expiration_date,
        //   certificate_file: await getFileFromUrl(res.data.certificate_file, res.data.fileName),
        // };
        this.setCarrierInsurance(res.data);
        this.$notificationSuccess('Your profile was successfully updated');
        window.location.reload();
      }).catch(() => {
        this.$notificationError('Your profile wasn\'t updated');
      }).finally(async () => {
        this.form.values = {
          agent_name: this.carrierInsurance.agent_name,
          agent_phone: this.carrierInsurance?.agent_phone.startsWith('+1') ? this.carrierInsurance.agent_phone.slice(2) : this.carrierInsurance.agent_phone,
          cargo_limit: this.carrierInsurance.cargo_limit,
          deductible: this.carrierInsurance.deductible,
          expiration_date: this.carrierInsurance.expiration_date,
          certificate_file: await getFileFromUrl(this.carrierInsurance.certificate_file, this.fileName),
        };
      });
    },
  },
};
</script>
